* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
  background: #0f0f0f;
  color: #e5e5e5;
  min-height: 100vh;
  line-height: 1.5;
}

.app {
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #0f0f0f 0%, #1a1a1a 50%, #0f0f0f 100%);
}

/* Header */
.app-header {
  text-align: center;
  margin-bottom: 40px;
}

.app-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 8px;
  background: linear-gradient(135deg, #22c55e, #16a34a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.app-subtitle {
  font-size: 1.1rem;
  color: #a3a3a3;
  font-weight: 400;
}

/* Main content */
.main-content {
  max-width: 1200px;
  margin: 0 auto;
}

/* Search section */
.search-section {
  margin-bottom: 30px;
}

.search-bar {
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}

.search-bar svg {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #71717a;
  z-index: 2;
}

.search-input {
  width: 100%;
  padding: 15px 15px 15px 45px;
  background: rgba(38, 38, 38, 0.8);
  border: 1px solid rgba(115, 115, 115, 0.3);
  border-radius: 12px;
  color: #e5e5e5;
  font-size: 1rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.search-input::placeholder {
  color: #71717a;
}

.search-input:focus {
  outline: none;
  border-color: #22c55e;
  box-shadow: 0 0 0 2px rgba(34, 197, 94, 0.2);
  background: rgba(38, 38, 38, 0.95);
}

/* Filter tabs */
.filter-tabs {
  margin-bottom: 40px;
}

.filter-title {
  font-size: 1.2rem;
  margin-bottom: 15px;
  text-align: center;
  color: #d4d4d8;
  font-weight: 500;
}

.filter-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.filter-btn {
  padding: 10px 20px;
  background: rgba(38, 38, 38, 0.8);
  border: 1px solid rgba(115, 115, 115, 0.3);
  border-radius: 25px;
  color: #d4d4d8;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.filter-btn:hover {
  background: rgba(64, 64, 64, 0.8);
  border-color: rgba(115, 115, 115, 0.5);
  transform: translateY(-1px);
}

.filter-btn.active {
  background: #22c55e;
  border-color: #22c55e;
  color: #0f0f0f;
  font-weight: 600;
}

.filter-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* Openings section */
.openings-section {
  margin-bottom: 40px;
}

.section-title {
  font-size: 1.8rem;
  margin-bottom: 30px;
  text-align: center;
  color: #e5e5e5;
  font-weight: 600;
}

.openings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
  justify-items: center;
}

/* Opening card */
.opening-card {
  background: rgba(38, 38, 38, 0.9);
  border: 1px solid rgba(115, 115, 115, 0.2);
  border-radius: 16px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  width: 100%;
  max-width: 320px;
  position: relative;
  overflow: hidden;
}

.opening-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, #22c55e, transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.opening-card:hover {
  transform: translateY(-3px);
  border-color: rgba(34, 197, 94, 0.4);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  background: rgba(45, 45, 45, 0.95);
}

.opening-card:hover::before {
  opacity: 1;
}

.opening-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.opening-info {
  flex: 1;
}

.opening-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 5px;
  line-height: 1.3;
  color: #f5f5f5;
}

.opening-moves {
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
  font-size: 0.9rem;
  color: #a3a3a3;
  font-weight: 400;
}

.eco-code {
  background: #22c55e;
  color: #0f0f0f;
  padding: 4px 10px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 0.85rem;
  letter-spacing: 0.5px;
}

.board-container {
  margin: 15px 0;
  display: flex;
  justify-content: center;
}

/* Progress indicator (new component) */
.progress-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 10px;
  font-size: 0.85rem;
  color: #a3a3a3;
}

.progress-bar {
  width: 60px;
  height: 4px;
  background: rgba(115, 115, 115, 0.3);
  border-radius: 2px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #22c55e;
  border-radius: 2px;
  transition: width 0.3s ease;
}

/* Status badge (new component) */
.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 3px 8px;
  border-radius: 6px;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.complete {
  background: rgba(34, 197, 94, 0.15);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.status-badge.in-progress {
  background: rgba(249, 115, 22, 0.15);
  color: #f97316;
  border: 1px solid rgba(249, 115, 22, 0.3);
}

.status-badge.not-started {
  background: rgba(115, 115, 115, 0.15);
  color: #737373;
  border: 1px solid rgba(115, 115, 115, 0.3);
}

