.tab-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: #a3a3a3;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 6px 6px 0 0;
}

.tab-btn:hover {
  background: rgba(115, 115, 115, 0.1);
  color: #d4d4d8;
}

.tab-btn.active {
  color: #22c55e;
  border-bottom-color: #22c55e;
  background: rgba(34, 197, 94, 0.05);
}

.tab-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.tab-icon {
  font-size: 1rem;
}

.tab-label {
  font-size: 0.95rem;
}

.tab-count {
  font-size: 0.8rem;
  opacity: 0.7;
}

.tab-content {
  margin-top: 20px;
}

/* Variations Content */
.variations-content {
  animation: fadeIn 0.3s ease;
}

.variations-header {
  margin-bottom: 25px;
}

.variations-subtitle {
  color: #a3a3a3;
  font-size: 0.95rem;
  margin-top: 5px;
}

.variations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}

/* Variation Card */
.variation-card {
  background: rgba(64, 64, 64, 0.4);
  border: 1px solid rgba(115, 115, 115, 0.3);
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
}

.variation-card:hover {
  background: rgba(64, 64, 64, 0.6);
  border-color: #22c55e;
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.variation-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.variation-info {
  flex: 1;
}

.variation-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #f5f5f5;
  margin-bottom: 5px;
}

.variation-moves {
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
  font-size: 0.9rem;
  color: #a3a3a3;
  margin-bottom: 10px;
}

.variation-badges {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: flex-end;
}

.variation-eco {
  background: #22c55e;
  color: #0f0f0f;
  padding: 4px 8px;
  border-radius: 6px;
  font-weight: 700;
  font-size: 0.75rem;
  letter-spacing: 0.5px;
}

.difficulty-badge.small {
  padding: 3px 8px;
  font-size: 0.7rem;
}

.popularity-badge {
  padding: 3px 8px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

/* Frequency Bar */
.variation-frequency {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 8px;
}

.frequency-label {
  font-size: 0.8rem;
  color: #a3a3a3;
  min-width: 65px;
}

.frequency-bar {
  flex: 1;
  height: 6px;
  background: rgba(115, 115, 115, 0.3);
  border-radius: 3px;
  overflow: hidden;
  max-width: 60px;
}

.frequency-fill {
  height: 100%;
  background: linear-gradient(90deg, #22c55e, #16a34a);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.frequency-text {
  font-size: 0.8rem;
  color: #22c55e;
  font-weight: 600;
  min-width: 30px;
}

.variation-board {
  margin: 15px 0;
  display: flex;
  justify-content: center;
}

.variation-description {
  font-size: 0.9rem;
  line-height: 1.5;
  color: #d4d4d8;
  margin-top: 10px;
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Overview content animation */
.overview-content {
  animation: fadeIn 0.3s ease;
}

/* Responsive variations */
@media (max-width: 768px) {
  .variations-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .variation-card {
    padding: 15px;
  }
  
  .variation-header {
    flex-direction: column;
    gap: 10px;
  }
  
  .variation-badges {
    flex-direction: row;
    align-items: center;
  }
  
  .detail-tabs {
    overflow-x: auto;
    white-space: nowrap;
  }
  
  .tab-btn {
    padding: 10px 16px;
    font-size: 0.9rem;
  }
}

.detail-board-section {
  background: rgba(38, 38, 38, 0.9);
  border: 1px solid rgba(115, 115, 115, 0.2);
  border-radius: 16px;
  padding: 30px;
  backdrop-filter: blur(10px);
  text-align: center;
}

.board-action-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 20px;
}

.detail-action-btn {
  padding: 12px 24px;
  background: rgba(38, 38, 38, 0.8);
  border: 1px solid rgba(115, 115, 115, 0.3);
  border-radius: 8px;
  color: #d4d4d8;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  min-width: 140px;
}

.detail-action-btn:hover {
  background: rgba(64, 64, 64, 0.8);
  border-color: rgba(115, 115, 115, 0.5);
  transform: translateY(-1px);
}

.detail-action-btn.active {
  background: rgba(34, 197, 94, 0.15);
  border-color: #22c55e;
  color: #22c55e;
}

.detail-favorite-btn {
  margin-top: 20px;
  padding: 12px 24px;
  background: rgba(38, 38, 38, 0.8);
  border: 1px solid rgba(115, 115, 115, 0.3);
  border-radius: 8px;
  color: #d4d4d8;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s ease;
  margin-left: auto;
  margin-right: auto;
}

.detail-favorite-btn:hover {
  background: rgba(64, 64, 64, 0.8);
  border-color: rgba(115, 115, 115, 0.5);
}

.detail-favorite-btn.active {
  background: rgba(34, 197, 94, 0.15);
  border-color: #22c55e;
  color: #22c55e;
}

/* Loading and error states */
.loading, .error {
  text-align: center;
  padding: 60px 20px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(115, 115, 115, 0.3);
  border-left: 3px solid #22c55e;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.no-results {
  text-align: center;
  padding: 60px 20px;
  color: #71717a;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(38, 38, 38, 0.5);
}

::-webkit-scrollbar-thumb {
  background: rgba(115, 115, 115, 0.5);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(115, 115, 115, 0.7);
}

/* Responsive design */
@media (max-width: 1024px) {
  .detail-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .detail-view {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .app {
    padding: 15px;
  }
  
  .app-title {
    font-size: 2rem;
  }
  
  .openings-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .filter-buttons {
    gap: 8px;
  }
  
  .filter-btn {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
  
  .detail-meta {
    flex-direction: column;
    gap: 10px;
  }
  
  .detail-info, .detail-board-section {
    padding: 25px;
  }
  
  .detail-title {
    font-size: 1.6rem;
  }
  
  .detail-moves {
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  .app {
    padding: 10px;
  }
  
  .detail-info, .detail-board-section {
    padding: 20px;
  }
  
  .detail-title {
    font-size: 1.4rem;
  }
  
  .detail-moves {
    font-size: 0.95rem;
  }
  
  .detail-header {
    margin-bottom: 20px;
  }
  
  .detail-content {
    gap: 20px;
  }
  
  .board-action-buttons {
    flex-direction: column;
    gap: 10px;
  }
  
  .detail-action-btn {
    min-width: auto;
    width: 100%;
  }
}

@media (max-width: 480px) {
  .opening-card {
    max-width: 100%;
  }
  
  .detail-info, .detail-board-section {
    padding: 15px;
  }
  
  .detail-title {
    font-size: 1.3rem;
  }
  
  .detail-moves {
    font-size: 0.9rem;
  }
  
  .back-btn {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
  
  .detail-eco {
    padding: 6px 12px;
    font-size: 0.9rem;
  }
  
  .board-action-buttons {
    flex-direction: column;
    gap: 8px;
  }
  
  .detail-action-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
    width: 100%;
  }
}

/* Remove old conflicting styles completely */
.detail-favorite-btn {
  display: none !important;
}

/* Chessboard responsive scaling */
@media (max-width: 480px) {
  /* Scale down card chessboards slightly on very small screens */
  .board-container {
    transform: scale(0.95);
  }
}

@media (max-width: 400px) {
  .board-container {
    transform: scale(0.9);
  }
}